html {
  font-size: 10px;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

strong {
  font-weight: bold;
}

@keyframes spin {
  0% {
    transform: rotate(0turn);
  }
  100% {
    transform: rotate(1turn);
  }
}
body {
  font-family: "effra", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: var(--dark-blue);
}
body:not(.wp-admin) {
  font-size: 1.8rem;
}

ul {
  padding-left: 2rem;
}

li {
  margin: 1rem 0;
  margin-left: 1.5rem;
}

a:hover {
  text-decoration: underline;
}

h1 {
  font-size: 3.2rem;
  margin-top: 0;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 5.6rem;
  }
}

h2 {
  margin: 1.5rem 0;
  font-size: 2.4rem;
  color: var(--blue);
}
@media (min-width: 768px) {
  h2 {
    font-size: 3rem;
  }
}

h3,
.editor-styles-wrapper .wp-block h3 {
  font-size: 2rem;
  margin: 1rem 0;
  color: var(--dark-green);
}
@media (min-width: 768px) {
  h3,
  .editor-styles-wrapper .wp-block h3 {
    font-size: 2.8rem;
  }
}

p {
  line-height: 1.5;
}

.bold {
  font-weight: bold;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-wrap-balance {
  text-wrap: balance !important;
}

.color-white {
  color: white;
}

.uppercase {
  text-transform: uppercase;
}

.max-width-100 {
  max-width: 100%;
}
.max-width-100 input {
  max-width: 100%;
}

.is-style-outline > .wp-block-button__link {
  border-color: inherit;
}

.mt-1 {
  margin-top: 1rem;
}

.mt-2 {
  margin-top: 2rem;
}

.mt-3 {
  margin-top: 3rem;
}

.mt-4 {
  margin-top: 4rem;
}

.mt-5 {
  margin-top: 5rem;
}

.mb-1 {
  margin-bottom: 1rem;
}

.mb-2 {
  margin-bottom: 2rem;
}

.mb-3 {
  margin-bottom: 3rem;
}

.mb-4 {
  margin-bottom: 4rem;
}

.mb-5 {
  margin-bottom: 5rem;
}

.content-width {
  width: 900px;
  max-width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: auto;
  margin-right: auto;
}

.page-content .wp-block-columns,
.wp-block-group__inner-container .wp-block-columns {
  margin-bottom: 0;
}
.page-content > *,
.wp-block-group__inner-container > * {
  width: 900px;
  max-width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: auto;
  margin-right: auto;
}
.page-content > *.alignwide,
.wp-block-group__inner-container > *.alignwide {
  width: 1200px;
}
.page-content > *.alignfull,
.wp-block-group__inner-container > *.alignfull {
  width: 100%;
  margin: 0;
  padding: 0;
}
.page-content > *.alignfull > .wp-block-column,
.wp-block-group__inner-container > *.alignfull > .wp-block-column {
  margin: 0;
}

.wp-block-columns {
  gap: 4rem;
}

body {
  min-height: 100vh;
}
body.admin-bar {
  min-height: calc(100vh - 46px);
}
@media (min-width: 768px) {
  body.admin-bar {
    min-height: calc(100vh - 32px);
  }
}
@media (min-width: 768px) {
  body {
    padding: 2rem;
  }
  body::before {
    content: "";
    background-image: var(--background-image);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;
  }
  body::after {
    content: "";
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: var(--light-blue);
    mix-blend-mode: screen;
    z-index: 0;
  }
}

.site-container {
  background: white;
  width: 1000px;
  max-width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: auto;
  margin-right: auto;
  min-height: 90vh;
  box-shadow: var(--blue) 0 2px 4px;
  position: relative;
  z-index: 1;
}

body {
  --wp--preset--spacing--20: 1rem;
  --wp--preset--spacing--30: 2rem;
  --wp--preset--spacing--40: 3rem;
  --wp--preset--spacing--50: 4rem;
  --wp--preset--spacing--60: 5rem;
  --wp--preset--spacing--70: 6rem;
  --wp--preset--spacing--80: 7rem;
}

.xdebug-error {
  z-index: 2;
  position: relative;
}

.page-content {
  position: relative;
}
.page-content .watermark-image {
  position: absolute;
  left: 0;
  top: 75%;
  transform: translateY(-50%);
  opacity: 0.1;
  z-index: 0;
  width: 60%;
}
.page-content > * {
  position: relative;
  z-index: 1;
}

.blog .post {
  display: block;
  width: 900px;
  max-width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: auto;
  margin-right: auto;
  color: var(--dark-blue);
  text-decoration: none;
  margin-bottom: 5rem;
}
.blog .post__title {
  color: var(--blue);
}
.blog .post__read-more {
  text-transform: uppercase;
  text-align: center;
  display: flex;
  align-items: center;
  color: var(--blue);
  gap: 2rem;
}
.blog .post__read-more::before, .blog .post__read-more::after {
  content: "";
  flex: 1 auto;
  height: 1px;
  background: var(--teal);
}

.header {
  position: relative;
  display: flex;
  flex-direction: column;
}

.header-logo {
  display: flex;
  justify-content: center;
}
.header-logo__logo {
  width: 300px;
}

.nav-toggle {
  display: none;
}
.header-nav {
  margin: 1rem 0;
  width: 100%;
}
@media (min-width: 768px) {
  .header-nav {
    position: absolute;
    height: 100%;
    top: 40px;
    left: 0;
  }
}
.header-nav__menu {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}
@media (min-width: 768px) {
  .header-nav__menu {
    display: grid;
    grid-template-columns: 1fr 1fr 300px 1fr 1fr;
  }
}
.header-nav .menu-item {
  flex-grow: 1;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .header-nav .menu-item:nth-child(1) {
    grid-column: 1/2;
  }
  .header-nav .menu-item:nth-child(2) {
    grid-column: 2/3;
  }
  .header-nav .menu-item:nth-child(3) {
    grid-column: 4/5;
  }
  .header-nav .menu-item:nth-child(4) {
    grid-column: 5/6;
  }
}
.header-nav .menu-item a {
  height: 50px;
  background: var(--blue);
  color: var(--light);
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  text-align: center;
}
.header-nav .menu-item a::before {
  content: "";
  position: absolute;
  left: 1rem;
  right: 1rem;
  top: 0.5rem;
  bottom: 0.5rem;
  border-bottom: 0.5px solid var(--light);
  border-top: 0.5px solid var(--light);
}
@media (min-width: 768px) {
  .header-nav .menu-item a {
    padding: 1rem 2rem;
    display: inline-flex;
  }
}

.footer {
  padding: 3rem 1rem;
}
.footer__logo {
  margin-left: auto;
  margin-right: auto;
}

.page-title {
  width: 900px;
  max-width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: var(--green);
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}
.page-title h1 {
  text-align: center;
  padding: 0 6rem;
  border-bottom: 1px solid var(--teal);
}

.pagination {
  flex: 0 0 100%;
  max-width: 100%;
  text-align: center;
  height: 50px;
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin: 1rem 0;
}
.pagination .page-numbers {
  font-family: "effra", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: var(--green);
  font-size: 1.8rem;
  border: 1px solid var(--green);
  transition: 0.25s;
  text-decoration: none;
  flex: 0 0 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pagination .page-numbers.current {
  background-color: rgba(var(--rgb-green), 0.1);
}
.pagination a.page-numbers:hover {
  background-color: var(--green);
  color: white;
  text-decoration: none;
}

.wp-block-separator {
  max-width: 100%;
  padding: 0;
}